/**
 *  3
 * @param {*} num 
 * @param {*} precision 
 * @param {*} separator 
 */
function formatNumber(num, precision, separator) {
  let parts = ''
  // 
  if (!isNaN(parseFloat(num)) && isFinite(num)) {
    num = Number(num)
    // 
    num = (typeof precision !== 'undefined' ? num.toFixed(precision) : num).toString()
    // 
    parts = num.split('.')
    // [separator], 
    parts[0] = parts[0].toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1' + (separator || ','))
    return parts.join('.')
  }
  return null
}

/**
 * 
 * @param {*} string : 123,456.78
 * @param {*} separator ,
 * @param {*} precision 
 */
function parseNumber(string, separator, precision = 8) {
  let parts = string.split('.')
  parts[0] = parts[0].replace(new RegExp(separator || ',', 'g'), '')
  return Number(Number(parts.join('.')).toFixed(precision))
}

function cutNum(value, integerNum) {
  var arrNum = value.split('.')
  if (arrNum.length > 0) {
    var integerNumber = arrNum[0].substr(0, integerNum)
    if (arrNum.length > 1) {
      value = integerNumber + '.' + arrNum[1]
    } else {
      value = integerNumber
    }
  }
  return value
}

// eslint-disable-next-line no-extend-native
Number.prototype.toFixedSelf = function(n) {
  if (n > 20 || n < 0) {
    throw new RangeError('toFixed() digits argument must be between 0 and 20')
  }
  const number = this
  if (isNaN(number) || number >= Math.pow(10, 21)) {
    return number.toString()
  }
  if (typeof (n) === 'undefined' || n == 0) {
    return (Math.round(number)).toString()
  }
  let result = number.toString()
  const arr = result.split('.')
  // 
  if (arr.length < 2) {
    result += '.'
    for (let i = 0; i < n; i += 1) {
      result += '0'
    }
    return result
  }
  const integer = arr[0]
  const decimal = arr[1]
  if (decimal.length == n) {
    return result
  }
  if (decimal.length < n) {
    for (let i = 0; i < n - decimal.length; i += 1) {
      result += '0'
    }
    return result
  }
  result = integer + '.' + decimal.substr(0, n)
  const last = decimal.substr(n, 1)

  // 
  if (parseInt(last, 10) >= 5) {
    const x = Math.pow(10, n)
    result = (Math.round((parseFloat(result) * x)) + 1) / x
    result = result.toFixedSelf(n)
  }
  return result
}

